import React, { Fragment, useState } from "react";
import { Card, Col, Container, Row } from "react-bootstrap";
import MyVerticallyCenteredModal from "./mailpopup";

const JobListing = () => {
  const jobData = [
    {
      id: "1",
      position: "Frontend Developer",
      department: "Development",
      type: "Intership",
      duration: "3 months",
      benefits: "Certification",
    },
    {
      id: "2",
      position: "SEO Analyst",
      department: "Marketing",
      type: "Internship",
      duration: "3 months",
      benefits: "Certification",
    },
    {
      id: "3",
      position: "Backend Developer",
      department: "Backend",
      type: "Internship",
      duration: "3 months",
      benefits: "Certification",
    },
    {
      id: "4",
      position: "Sales Development",
      department: "Sales",
      type: "Intership",
      duration: "3 months",
      benefits: "Certification",
    },
    {
      id: "5",
      position: "Content Writing",
      department: "Marketing",
      type: "Intership",
      duration: "3 months",
      benefits: "Certification",
    },
    {
      id: "6",
      position: "Digital Marketing",
      department: "Marketing",
      type: "Intership",
      duration: "3 months",
      benefits: "Certification",
    },
  ];

  const [isOpen, setIsOpen] = useState(false);

  return (
    <Fragment>
      <section className="job_listings_wrapper">
        <Container>
          <Row className="rg_50">
            {jobData.map((job) => {
              return (
                <Col
                  lg={4}
                  sm={6}
                  className="d-flex align-items-center justify-content-center"
                >
                  <Card
                    className="joblist_cards"
                    key={job.id}
                    onClick={() => {
                      setIsOpen(true);
                    }}
                  >
                    <Card.Header>{job.position}</Card.Header>
                    <Card.Body>
                      <p>
                        Deparment : <span>{job.department}</span>
                      </p>
                      <p>
                        Job Type : <span>{job.type}</span>
                      </p>
                      <p>
                        Duration : <span>{job.duration}</span>
                      </p>
                      <p>
                        Benefits : <span>{job.benefits}</span>
                      </p>
                    </Card.Body>
                  </Card>
                </Col>
              );
            })}
          </Row>
        </Container>
      </section>
      <MyVerticallyCenteredModal
        show={isOpen}
        onHide={() => setIsOpen(false)}
      />
    </Fragment>
  );
};

export default JobListing;
