import React from "react";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import Layout from "../../components/layout";
import Seo from "../../components/seo";
import BreadCrumb from "../../components/common/breadcrumb";
import JobListing from "../../screens/careers/joblisting";

const Careers = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Careers", link: null },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <JobListing />
    </Layout>
  );
};

export const Head = () => {
  return (
    <Seo
      title="Current Openings @ Justtry Technologies"
      headTitle="Current Openings @ Justtry Technologies"
      description="Looking for a career opportunity at Builder.ai? Find out the best job opportunity for you and grow your career with one of the most innovative companies in the AI space."
      keywords="Career opportunities, Job openings, Employment opportunities, Work culture, Professional development, Company values, Team collaboration, Growth potential, Talent acquisition, Job vacancies "
    />
  );
};

export default Careers;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
